import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AddCard from "./card";
import SectionHeading from "./section-head";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import { IoMdArrowBack, IoMdArrowForward } from "react-icons/io";

import image1 from "../../images/post-1.jpg";
import image2 from "../../images/post-2.jpg";
import image3 from "../../images/post-3.jpg";

const images = [
    image1, image2, image3
]

const Slide = () => {
    const card = images.map(element => {
        return <AddCard image={element} />
    })
    return card;
}


const useStyles = makeStyles((theme) => ({
    wrapper: {
        padding: "4rem 2rem",
        background: "var(--primary)"
    },
}));

export default function SpecialOffers(props) {
    const classes = useStyles();

    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 4000,
        prevArrow: <IoMdArrowBack/>,
        nextArrow: <IoMdArrowForward/>,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    infinite: true
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
            // You can unslick at a given breakpoint now by adding:
            // settings: "unslick"
            // instead of a settings object
        ]
    };

    return (
        <div className={classes.wrapper}>
            <SectionHeading text="Special offers" />
            <Slider {...settings} className={classes.slider, "special-offer"}>
                <AddCard image={image1} />
                <AddCard image={image2}/>
                <AddCard image={image3}/>
                <AddCard image={image1}/>
                <AddCard image={image2}/>
                <AddCard image={image3}/>
            </Slider>
        </div>
    );
}
