import React from "react";
import "./body/body.css";
import { MdLibraryAdd } from "react-icons/md";
import Banner from "./body/banner";
import SpecialOffers from "./body/special-offers";

class Body extends React.Component{
    render () {
        return (
            <div>
                <MdLibraryAdd className="icons add-post-btn ripple-always" />
                <Banner />
                <SpecialOffers />
            </div>
        )
    }
}

export default Body;