import React from "react";
import { HiFire } from "react-icons/hi";


export default function Copyright (props) {
    return (
        <div className="copyright">
            <div className="company">
                <div>
                    <span><b><HiFire className="company-icon" />TraceTechie</b></span>
                    <span>Copyright Â© 2019-2020 TraceTechie || All rights reserved.</span>
                </div>
            </div>
            <Projects />
        </div>
    )
}

function Projects (props) {
    return (
        <div className="projects">
            <div className="company-name">
                Tracetechie company projects
            </div>
            <ul className="project-list">
                <li><a href="https://www.farmapropos.com">Farmapropos</a></li>
                <li><a href="https://tracetechie.github.io/tracemall">Tracemall</a></li>
                <li><a href="https://tracetechie.github.io/tracetechie">Tracetechie</a></li>
            </ul>
        </div>
    )
}